<template>
    <div class="users_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input type="text" v-model="queryInfo.query" placeholder="" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" style="width: 100%" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
            <!-- <template slot-scope="scope"> -->
            <!-- vue2.6 v-slot# -->
            <!-- <template v-slot="scope"> -->
            <template #default="scope">
                <el-switch v-model="scope.row.mg_state" @change="userStateChange(scope.row)">
                </el-switch>
            </template>
        </el-table-column>
        <el-table-column label="" width="180px">
            <template v-slot="scope">
                <!--  -->
                <el-button type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)" size="mini">
                    
                </el-button>
                <!--  -->
                <el-button type="danger" @click="removeUserById(scope.row.id)" icon="el-icon-delete" size="mini"></el-button>
                <!--  -->
                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
                </el-tooltip>
            </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="userlist.total">
      </el-pagination> -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
    </el-pagination>
    </el-card>
     <!--  -->
    <el-dialog title="" @close="addDialogClosed" :visible.sync="addDialogVisible" width="50%">
    <!--  -->
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" status-icon label-width="70px" class="demo-ruleForm">
    <el-form-item label="" prop="username">
        <el-input type="text" v-model="addForm.username" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
        <el-input type="password" v-model="addForm.password" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" prop="email">
        <el-input v-model="addForm.email"></el-input>
    </el-form-item>
    <el-form-item label="" prop="mobile">
        <el-input v-model="addForm.mobile" ></el-input>
    </el-form-item>
    </el-form>
    <!--  -->
    <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible=false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
    </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="editDialogVisible" @close="editDialogClosed" width="30%">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" status-icon label-width="70px" class="demo-ruleForm">
        <el-form-item label="">
        <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
            <el-input v-model="editForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editUser"> </el-button>
        </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="setRoleDialogVisible" width="30%" @close="setRoleDialogClosed">
        <div>
        <p>:{{userInfo.username}}</p>
        <p>:{{userInfo.role_name}}</p>
        <p>:
            <el-select v-model="selectedRoleId" placeholder="">
            <!-- :label :value  -->
            <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
            </el-option>
            </el-select>
        </p>
        </div>
        <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>
           
<script>
export default{
    data(){
        // 
        var checkEmail = (rule, value, cb) => {
        // 
        const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if (regEmail.test(value)) {
            // 
            return cb()
        }
        cb(new Error(''))
        }
        // 
        var checkMobile = (rule, value, cb) => {
        // 
        const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if (regMobile.test(value)) {
            return cb()
        }
        cb(new Error(''))
    }
        return {
            userlist: [],
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:5
            },
            total:0,
            addDialogVisible: false,
            editDialogVisible: false,
            setRoleDialogVisible: false,
            userInfo:{},
            rolesList:[],
            selectedRoleId:'',
            addForm:{
                username:'',
                password:'',
                email:'',
                mobile:''
            },
            addFormRules:{
                username:[
                    {required:true,message:'',trigger:'blur'},
                    {min:3,max:15,message:'3~15',trigger:'blur'}
                ],
                password:[
                    {required:true,message:'',trigger:'blur'},
                    {min:3,max:15,message:'3~15',trigger:'blur'}
                ],
                email:[
                    {required:true,message:'',trigger:'blur'},
                    { validator: checkEmail, trigger: 'blur' }
                ],
                mobile:[
                    {required:true,message:'',trigger:'blur'},
                    { validator: checkMobile, trigger: 'blur' }
                ]
            },
            editForm: {
                username: '',
                email:'',
                mobile:''
            }, // 
            editFormRules: { // 
            email: [
                { required: true, message: '', trigger: 'blur' },
                { validator: checkEmail, trigger: 'blur' }
            ],
            mobile: [
                { required: true, message: '', trigger: 'blur' },
                { validator: checkMobile, trigger: 'blur' }
            ]
            }
        }
    },
    methods:{
        async getUserList(){
            const {data:res} = await this.$http.get('users',{
                params:this.queryInfo
            })
            console.log(res)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.userlist = res.data.users
            this.total= res.data.total
        },
         //  pagesize 
        handleSizeChange(newSize) {
            // console.log(newSize)
                this.queryInfo.pagesize = newSize
                this.getUserList()
        },
        //   
        handleCurrentChange(newPage) {
            console.log(newPage)
            this.queryInfo.pagenum = newPage
            this.getUserList()
        },
        async userStateChange(userinfo){
            console.log(userinfo)
            const {data:res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
            if(res.meta.status !== 200){
                userinfo.mg_state = !userinfo.mg_state
                return this.$message.error('!')
            }
            this.$message.success('')
        },
        addDialogClosed() { // 
            this.$refs.addFormRef.resetFields()
        },
        addUser(){
            this.$refs.addFormRef.validate(async valid=>{
                if(!valid) return
                const {data:res} = await this.$http.post('users',this.addForm)
                if(res.meta.status !== 201){
                    this.$message.error('')
                }
                this.$message.success('')
                this.addDialogVisible = false
                this.getUserList()
            })
        },
        async removeUserById(id){
            const confirmResult = await this.$confirm('','',{
                confirmButtonText:'',
                cancelButtonText:'',
                type:'warnimg'
            }).catch(err => err)

            if(confirmResult != "confirm"){
                return this.$message.info('')
            }
            const {data:res} = await this.$http.delete('users/'+id)

            if(res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')

            this.getUserList()
        },
        async showEditDialog (id) { // 
            const {data:res} = await this.$http.get('users/'+id)
            console.log(res)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.editForm = res.data
            this.editDialogVisible = true
            
        },
        editDialogClosed() { // 
            this.$refs.editFormRef.resetFields()
        },
        editUser(){
            this.$refs.editFormRef.validate(async valid=>{
                if(!valid) return
                const {data:res} = await this.$http.put('users/'+this.editForm.id,this.editForm)
                if(res.meta.status !== 200){
                    this.$message.error('')
                }
                this.$message.success('')
                this.editDialogVisible = false
                this.getUserList()
            })
        },
        async setRole( userInfo ){
            console.log(userInfo)
            //
            this.userInfo = userInfo;
            //
            //id
            const { data: res } = await this.$http.get('roles')
            //
            if (res.meta.status !== 200) return this.$message.error('')
            
            this.rolesList = res.data;
            //
            this.setRoleDialogVisible = true;
        },
        async saveRoleInfo(){
            if(!this.selectedRoleId){
                return this.$message.error('')
            }
            const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.$message.success('')
            this.getUserList()
            this.setRoleDialogVisible = false
        },
        setRoleDialogClosed(){
            this.selectedRoleId = ''
            this.userinfo = {}
        }
    },
    created(){
        this.getUserList()
    }   
}
</script>
<style lang="less" scoped>
     .el-input__inner {
         width: 100px;
     }   
     p {
         margin: 10px;
         text-align: left;
     }    
</style>