<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!--  -->
            <el-alert title="" type="info" center show-icon :closable="false">
            </el-alert>
            <!--  -->
            <!-- align-center() -->
            <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
                <el-tabs tab-position="left" v-model="activeIndex" :before-leave="beforeTabLeave" @tab-click="tabClicked">
                    <el-tab-pane label="" name="0">
                        <el-form-item label="" prop="goods_name">
                            <el-input v-model="addForm.goods_name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_price">
                            <el-input v-model="addForm.goods_price"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_weight">
                            <el-input v-model="addForm.goods_weight"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_number">
                            <el-input v-model="addForm.goods_number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_cat">
                            <el-cascader expandTrigger='hover' v-model="addForm.goods_cat" :options="cateList" :props="cateProps" @change="handleChange" clearable></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="1">
                        <!-- item -->
                        <el-form-item :label="item.attr_name" :key="item.attr_id" v-for="item in manyTableData">
                            <!--  -->
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox border :label="val" v-for="(val,i) in item.attr_vals" :key="i"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="2">
                        <!--  -->
                            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                                <el-input v-model="item.attr_vals"></el-input>
                            </el-form-item>
                    </el-tab-pane>
                    <!--  -->
                    <el-tab-pane label="" name="3">
                        <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" :on-success="handleSuccess" list-type="picture" :headers="headerObj">
                            <el-button size="small" type="primary"></el-button>
                        </el-upload>
                    </el-tab-pane>
                    <el-tab-pane label="" name="4">
                        <quill-editor v-model="addForm.goods_introduce"></quill-editor>
                        <el-button type="primary" class="btnAdd" @click="add"></el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
            
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="previewVisible" width="40%">
        <img :src="previewPath" class="previewImg" />
        </el-dialog>
    </div>
</template>
           
<script>
import _ from 'lodash'
export default{
    data(){
        return {
            activeIndex: '0',
            addForm:{
                goods_name: '',
                goods_price: 0,
                goods_weight: 0,
                goods_number: 0,
                goods_cat:[],
                pics:[],
                goods_introduce:'',
                attrs:[]
            },
            addFormRules:{
                goods_name:[
                    {required:true,message:'',triggerL:'blur'}
                ],
                goods_price:[
                    {required:true,message:'',triggerL:'blur'}
                ],
                goods_weight:[
                    {required:true,message:'',triggerL:'blur'}
                ],
                goods_number:[
                    {required:true,message:'',triggerL:'blur'}
                ],
                goods_cat: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            //
            cateList: [],
            //
            cateProps: {
                value: 'cat_id',
                label: 'cat_name',
                children: 'children'
            },
            //
            manyTableData: [],
            //
            onlyTableData:[],
            //url
            uploadURL: 'http://www.klxin.cn:8888/api/private/v1/upload',
            //headers
            headerObj: { Authorization: window.localStorage.getItem('token') },
            //url
            previewPath: '',
            previewVisible:false
            
        }
    },
    created(){
        this.getCateList()
    },
    methods: {
        async getCateList(){
          const {data:res} = await this.$http.get('categories')
          if(res.meta.status !== 200){
              return this.$message.error('')
          }
          this.cateList = res.data
        },
        handleChange(){
            //,
            if(this.addForm.goods_cat.length !== 3){
                this.addForm.goods_cat = []
                return
            }
        },
        beforeTabLeave(activeName,oldActiveName){
            if(oldActiveName === '0'){
                if(this.addForm.goods_cat.length !== 3){
                    this.$message.error('')
                    return false
                }
            }
        },
        async tabClicked(){
            if(this.activeIndex === '1'){
                const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{ params: { sel: 'many' } })
                if(res.meta.status !== 200){
                    return this.$message.error('')
                }
                res.data.forEach(item=>{
                    item.attr_vals = item.attr_vals.length === 0 ?[]:item.attr_vals.trim().split(',')
                })
                this.manyTableData = res.data
            } else if(this.activeIndex === '2'){
                const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{ params: { sel: 'only' } })
                if(res.meta.status !== 200){
                    return this.$message.error('')
                }
                this.onlyTableData = res.data
            }
        },
        handlePreview(File){
            console.log(File)
            //
            this.previewPath = 'http://www.klxin.cn:8888/' + File.response.data.tmp_path
            this.previewVisible = true
        },
        handleRemove(file){
            //
            const filePath = file.response.data.tmp_path

            const index = this.addForm.pics.findIndex(item => item.pic === filePath)

            this.addForm.pics.splice(index,1)
        },
        handleSuccess(response){
            console.log(response)
            const picInfo = {pic:response.data.tmp_path}
            this.addForm.pics.push(picInfo)
        },
        add(){
            this.$refs.addFormRef.validate(async valid => {
                if(!valid) return this.$message.error('')
                //addFormgoods_cat
                const form = _.cloneDeep(this.addForm)
                form.goods_cat = form.goods_cat.join(",")

                console.log(form)

                this.manyTableData.forEach(item => {
                    form.attrs.push({attr_id:item.attr_id,attr_value:item.attr_vals.join(" ")})
                })
                this.onlyTableData.forEach(item => {
                    form.attrs.push({attr_id:item.attr_id,attr_value:item.attr_vals})
                })
                const {data:res} = await this.$http.post('goods',form)
                if(res.meta.status !== 201){
                    return this.$message.error('')
                }
                this.$message.success('')
                this.$router.push('/goods')
                
            })
        }
    },
    // 
    computed: {
        cateId() {
        if (this.addForm.goods_cat.length === 3) {
            return this.addForm.goods_cat[2]
        }
        return null
        }
    }         
}
</script>
<style lang="less" scoped>
.previewImg {
    width: 100%   
}

/* // */
.btnAdd {
  margin-top:15px
}       
</style>