<template>
    <div class="rights">
      <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="rightsList" stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="authName" label="">
        </el-table-column>
        <el-table-column prop="path" label="">
        </el-table-column>
        <el-table-column prop="level" label="">
            <template slot-scope="scope">
                <el-tag v-if="scope.row.level === '0'" ></el-tag>
                <el-tag v-if="scope.row.level === '1'" type="success"></el-tag>
                <el-tag v-if="scope.row.level === '2'" type="warning"></el-tag>
            </template>
        </el-table-column>
    </el-table>
    </div>
</template>
           
<script>
  export default {
    data() {
      return {
        rightsList: []
      }
    },
    methods:{
        async getRightList(){
            const {data:res} = await this.$http.get('rights/list')
            console.log(res)
            if(res.meta.status !== 200)
                return this.$messages.error('')
            this.rightsList = res.data
            
        }
    },
    created(){
        this.getRightList()
    }
  }
</script>
