<template>
    <div>
      <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <el-row>
            <el-col :span="8">
                <el-input v-model="queryInfo.query" placeholder="">
                    <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4">
                <el-button class="addGoods" type="primary" @click="goAddPage"></el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-table :data="goodsList" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column prop="goods_name" label=""></el-table-column>
                <el-table-column prop="goods_price" label="" width="95px"></el-table-column>
                <el-table-column prop="goods_weight" label="" width="95px"></el-table-column>
                <el-table-column prop="add_time" label="" width="200px">
                    <template slot-scope="scope">
                        {{scope.row.add_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="" width="125px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.goods_id)"></el-button>
                        <!--  -->
                        <el-button size="mini" type="danger" icon="el-icon-edit" @click="removeGoods(scope.row.goods_id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
        </el-row>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" @close="editDialogClosed" width="50%">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" status-icon label-width="100px" class="demo-ruleForm">
        <el-form-item label=""  prop="goods_name">
        <el-input v-model="editForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
            <el-input v-model="editForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
            <el-input v-model="editForm.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
            <el-input v-model="editForm.goods_number"></el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="edit"> </el-button>
        </span>
    </el-dialog>
    </div>
</template>
           
<script>
export default{
    data(){
        return {
            goodsList:[],
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            total:0,
            editDialogVisible:false,
            editForm:{
                goods_name:'',
                goods_price:'',
                goods_number:'',
                goods_weight:''
            },
            editFormRules:{
                goods_name:[
                    {required:true,message:'',trigger:'blur'}
                ],
                goods_price:[
                    {required:true,message:'',trigger:'blur'}
                ],
                goods_weight:[
                    {required:true,message:'',trigger:'blur'}
                ],
                goods_number:[
                    {required:true,message:'',trigger:'blur'}
                ]
            }
        }
    },
    created(){
        this.getGoodsList()
    },
    methods:{
        async getGoodsList(){
            const {data:res} = await this.$http.get('goods',{
                params:this.queryInfo
            })
            console.log(res)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            // console.log(res.data)
            this.goodsList = res.data.goods
            this.total = res.data.total
        },
        handleSizeChange(newSize){
            //pagesize
            this.queryInfo.pagesize = newSize
            this.getGoodsList()
        },
        handleCurrentChange(newPage){
            //pagesize
            this.queryInfo.pagenum = newPage
            this.getGoodsList()
        },
        async removeGoods(goods_id){
            //
            const confirmResult = await this.$confirm(
                '',
                '',
                {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }
            ).catch(err => err)
            //confirmResult 'confirm'
            //, confirmResultcatch'cancel'
            if (confirmResult != 'confirm') {
                return this.$message.info('')
            }
            const {data:res} = await this.$http.delete(`goods/${goods_id}`)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.$message.success('')
            this.getGoodsList()
        },
        goAddPage(){
            this.$router.push('/goods/add')
        },
        async showEditDialog(id){
            const {data:res} = await this.$http.get('goods/'+id)
            console.log(res)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.editForm = res.data
            this.editDialogVisible = true
        },
        editDialogClosed(){
            this.$refs.editFormRef.resetFields()
            console.log(this.editForm)
        },
        edit(){
            this.$refs.editFormRef.validate(async valid => {
                if(!valid) return
                const {data:res} = await this.$http.put('goods/'+this.editForm.goods_id,this.editForm)
                console.log(res)
                if(res.meta.status !== 200){
                    return this.$message.error('')
                }
                this.$message.success('')
                this.editDialogVisible = false
                this.getGoodsList()
            })
        }
    }   
}
</script>
<style lang="less" scoped>
.addGoods {
    margin-left: 20px;
}  
</style>