<template>
    <div>
      <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="roleList" border>
        <!--  -->
        <el-table-column type="expand">
            <template slot-scope="scope">
                <el-row :class="['bdbottom','vcenter',i1===0?'bdtop':'']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                    <el-col :span="5">
                        <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                         <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="19">
                        <el-row :class="[i2===0?'':'bdtop','vcenter' ]" v-for="(item2,i2) in item1.children" :key="item2.id">
                            <el-col :span="6">
                                <el-tag closable @close="removeRightById(scope.row,item2.id)" type="success">{{item2.authName}}</el-tag>
                                 <i class="el-icon-caret-right"></i>
                                </el-col>
                            <el-col :span="18">
                                <el-tag closable @close="removeRightById(scope.row,item3.id)" type="warning" v-for="(item3) in item2.children" :key="item3.id">
                                    {{item3.authName}}
                                </el-tag>
                            </el-col>
                        </el-row>
                    </el-col>
                    <!-- <pre>
                        pre:
                        {{scope.row}}
                    </pre> -->
                </el-row>
            </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
            <template slot-scope="scope"> 
                <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id,scope.row.roleName)"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRoleById(scope.row.id)"></el-button>
                <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
            </template>
        </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" @close="editDialogClosed" width="30%">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" status-icon label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="roleName">
        <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
        <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editRole"> </el-button>
        </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" @close="setRightDialogClose">
        <!-- <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree> -->
        <el-tree :data="rightsList" :props="treeProps" show-checkbox default-expand-all node-key="id" :default-checked-keys="defKeys" ref="treeRef"></el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="allotRights"> </el-button>
        </span>
    </el-dialog>
    </div>
</template>
           
<script>
export default{
    data(){
        return {
            roleList:[],
            dialogVisible: false,
            editDialogVisible:false,
            editForm:{
                roleName:'',
                roleDesc:''
            },
            editFormRules:{
                roleName:[{required:true,message:'',trigger:'blur'}],
                roleDesc:[{required:true,message:'',trigger:'blur'}]
            },
            //
            rightsList: [],
            treeProps:{
                label: 'authName',
                children:'children'
            },
            defKeys: [],
            //id
            roleId:''
        }
    },
    methods:{
        async getRoleList(){
            const {data:res} = await this.$http.get('roles')
            if(res.meta.status !== 200)
            return this.$message.error('')
            this.roleList = res.data
        },
        async removeRightById(role,rightId){
            const confirmResult = await this.$confirm('','',{
                confirmButtonText:'',
                cancelButtonText:'',
                type:'warning'
            }).catch(err => err)
            //confirmResult 'confirm'
            //, confirmResultcatch'cancel'
            if(confirmResult != "confirm"){
                return this.$message.info("")
            }
            //
            //delete
            const {data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
            if(res.meta.status !== 200)
            return this.$message.error('')
            //
            //
            role.children = res.data
            // this.getRoleList();
        },
        async showSetRightDialog(role) {
            this.roleId = role.id
        
            //
            const {data:res} = await this.$http.get('rights/tree')
            //
            if (res.meta.status !== 200)
                return this.$message.error('')
            //data
            this.rightsList = res.data
            this.getLeafKeys(role,this.defKeys)
            //
            this.dialogVisible = true;
        },
        showEditDialog(id){},
        async removeRoleById(id){
            const confirmResult = await this.$confirm('','',{
                confirmButtonText:'',
                cancelButtonText:'',
                type:'warnimg'
            }).catch(err => err)
            if(confirmResult != "confirm"){
                return this.$message.info('')
            }
            const {data: res } = await this.$http.delete('roles/'+id)
            if(res.meta.status !== 200) return this.$message.error('')
            this.$message.success('')
            this.getRoleList()
        },
        getLeafKeys(node,arr){
            if (!node.children) {
                return arr.push(node.id)
            }
            // 
            node.children.forEach(item => this.getLeafKeys(item, arr))
        },
        setRightDialogClose() {
            //
            this.defKeys = []
        },
        async allotRights() {
            //
            //

            //
            const keys = [
                ...this.$refs.treeRef.getCheckedKeys(),
                ...this.$refs.treeRef.getHalfCheckedKeys()
            ]
            // , 
            const idStr = keys.join(',')

            //
            const { data: res } = await this.$http.post(
                `roles/${this.roleId}/rights`,
                { rids:idStr }
            )
            if (res.meta.status !== 200)
                return this.$message.error('')

            this.$message.success("")
            this.getRoleList();
            //
            this.dialogVisible = false;
        },
        async showEditDialog(id,name){
            const {data:res} = await this.$http.get('roles/'+id)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.editForm = res.data
            console.log(this.editForm)
            this.getRoleList()
            this.editDialogVisible = true
        },
        editRole(){
            this.$refs.editFormRef.validate(async valid=>{
                if(!valid) return
                const {data:res} = await this.$http.put('roles/'+this.editForm.roleId,this.editForm)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.$message.success('')
                this.editDialogVisible = false
                this.getRoleList()
            })
        },
        editDialogClosed(){
            this.$refs.editFormRef.resetFields()
        }
    },
    created(){
        this.getRoleList()
    }   
}
</script>
<style lang="less" scoped>
.el-tag {
    margin: 15px;
}
.bdbottom {
    border-bottom: 1px solid #eee;
}
.bdtop {
    border-top: 1px solid #eee;
}  
.vcenter {
    display: flex;
    align-items: center;
}  
/deep/.el-dialog_body {
    max-height:500px;
    overflow: auto;
}  
</style>