<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!--  :closable="false"(X) show-icon() -->
            <el-alert title="" type="warning" :closable="false" show-icon>
            </el-alert>
            <!--  -->
            <el-row class="cat_opt">
                <el-col>
                    <span></span>
                    <!--  -->
                </el-col>
                <el-col></el-col>
            </el-row>
            <!--  -->
            <el-row class="cat_opt">
                <el-col>
                    <span></span>
                    <!--  -->
                    <el-cascader expandTrigger='hover' v-model="selectedCateKeys" :options="cateList" :props="cateProps" @change="handleChange" clearable></el-cascader>
                </el-col>
                <el-col></el-col>
            </el-row>
            <!-- tab -->
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
            <!--  many -->
            <el-tab-pane label="" name="many">
                <el-button size="mini" type="primary" :disabled="isButtonDisabled" @click="showDialog"></el-button>
                <!--  -->
                <el-table :data="manyTableData" border stripe>
                <!--  -->
                <el-table-column type="expand">
                     <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- el-tag -->
                        <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable>{{item}}</el-tag>
                        <!--  -->
                        <el-input class="input-new-tag" v-if="inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                        </el-input>
                        <!--  -->
                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </template>
                </el-table-column>
                </el-table-column>
                <!--  -->
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="attr_name"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)"></el-button>
                    <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                    </template>
                </el-table-column>
                </el-table>
            </el-tab-pane>
            <!--  only -->
            <el-tab-pane label="" name="only">
                <el-button size="mini" type="primary" :disabled="isButtonDisabled" @click="showDialog" @close="handleClose(i,scope.row)"></el-button>
                <!--  -->
                <el-table :data="onlyTableData" border stripe>
                <!--  -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- el-tag -->
                        <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable>{{item}}</el-tag>
                        <!--  -->
                        <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                        </el-input>
                        <!--  -->
                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="attr_name"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)"></el-button>
                    <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                    </template>
                </el-table-column>
                </el-table>
            </el-tab-pane>
            </el-tabs>
        </el-card>

        <!--  -->
        <el-dialog :title="''+titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
            <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addParams"> </el-button>
        </span>
        </el-dialog>

        <!--  -->
        <el-dialog :title="''+titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
            <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editParams"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>
           
<script>
export default{
    data(){
        return {
            cateList:[],
            //id
            selectedCateKeys:[],
            cateProps:{
                value: 'cat_id',
                label: 'cat_name',
                children: 'children'
            },
            activeName:'many',
            manyTableData:[],
            onlyTableData:[],
            addDialogVisible:false,
            addForm:{
                attr_name:''
            },
            addFormRules:{
                attr_name: [{ required: true, message: '', trigger: 'blur' }]
            },
            editDialogVisible:false,
            editForm:{
                attr_name:''
            },
            editFormRules:{
                attr_name: [{ required: true, message: '', trigger: 'blur' }]
            },
            inputVisible:false
        }
    },
    created(){
        this.getCateList()
    },
    methods:{
        // 
        async getCateList(){
            const {data:res} = await this.$http.get('categories')
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.cateList = res.data
        },
        async handleChange(){
            //
            this.getParamsData()
        },
        handleTabClick(){
            this.handleChange()
        },
        showDialog(){
            this.addDialogVisible=true
        },
        addDialogClosed(){},
         // 
        async getParamsData () {
            // 
            if (this.selectedCateKeys.length !== 3) {
                this.selectedCateKeys = []
                return
            }

            // 
            console.log(this.selectedCateKeys)
            // Id
            const { data: res } = await this.$http.get(
                `categories/${this.cateId}/attributes`,
                {
                params: { sel: this.activeName }
                }
            )

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            // attr_vals
            res.data.forEach(item => {
                item.attr_vals =
                item.attr_vals.length === 0 ? [] : item.attr_vals.trim().split(' ')
            })
            console.log(res.data)
            // this.tableData = res.data
            if (this.activeName === 'many') {
                this.manyTableData = res.data
            } else if (this.activeName === 'only') {
                this.onlyTableData = res.data
            }
        },
        addParams(){
            this.$refs.addFormRef.validate(async valid => {
                //return
                if (!valid) return
                //
                const {data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{
                    attr_name: this.addForm.attr_name, 
                    attr_sel: this.activeName,
                    attr_vals: "a,b,c"
                })
                if(res.meta.status !== 201){
                    return this.$message.error('' + this.titleText + '')
                }
                this.$message.success('' + this.titleText + '')
                this.addDialogVisible = false
                this.getParamsData()
            })
        },
        async showEditDialog(attr_id){
            const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,
            {params:{ attr_sel:this.activeName }})
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.editForm = res.data
            this.editDialogVisible=true
        },
        editParams(){
            this.$refs.editFormRef.validate(async valid => {
                if(!valid) return
                //
                const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
                {attr_name:this.editForm.attr_name,attr_sel:this.activeName})
                if (res.meta.status !== 200) {
                return this.$message.error('')
                }
                this.$message.success('' + this.titleText + '')
                this.editDialogVisible=false
                this.getParamsData()
            })
        },
        editDialogClosed(){
            //.
            this.$refs.editFormRef.resetFields()
        },
        async removeParams(attr_id){
             //
            const confirmResult = await this.$confirm(
                ''+this.titleText,
                '',
                {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }
            ).catch(err => err)
            if (confirmResult != 'confirm') {
                return this.$message.info('')
            }

            //
            const {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('' + this.titleText + '')
            this.getParamsData()
        },
        handleInputConfirm(row){
            //enter
            //
            if(row.inputValue.trim().length===0){
                row.inputValue = ''
                this.inputVisible = false
                return
            }

            row.attr_vals.push(row.inputValue.trim())
            row.inputValue = ''
            this.inputVisible = false

            this.saveAttrVals(row)
        },
        showInput(row){
            //
            this.inputVisible = true
           
        },
        handleClose(index,row){
            //
            row.attr_vals.splice(index,1)
            //
            this.saveAttrVals(row)
        },
        async saveAttrVals(row){
            //
            //
            const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,
            {attr_name:row.attr_name,attr_sel:row.attr_sel,attr_vals:row.attr_vals.join(' ')})

            if (res.meta.status !== 200) {
                return this.$message.error('')
            }

            this.$message.success('')
        }
    },
    computed: {
        //
        isButtonDisabled() {
        return this.selectedCateKeys.length !== 3
        },
        //id
        cateId() {
        if (this.selectedCateKeys.length === 3) {
            return this.selectedCateKeys[this.selectedCateKeys.length - 1]
        }
        return null
        },
        // 
        titleText () {
        if (this.activeName === 'many') {
            return ''
        }
        return ''
        }
    }   
}
</script>
<style lang="less" scoped>
.cat_opt{
    margin-top: 15px;
}       
</style>