<template>
    <div>
       <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
        <el-row>
            <el-col>
                <el-button type="primary" @click="showCateDialog"></el-button>
            </el-col>
        </el-row>
        <el-row>
            <tree-table :data="cateList" :columns="columns" :selection-type="false"
            :expand-type="false" show-index index-text="#" border :show-row-hover="false">
            <template slot="isok" slot-scope="scope">
                <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
                <i class="el-icon-error" v-else style="color:red"></i>
            </template>
            <template v-slot:order="scope">
                <el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
                <el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1"></el-tag>
                <el-tag size="mini" type="warning" v-else></el-tag>
            </template>
            <template v-slot:opt="scope">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click="showCayeDialog(scope.row.cat_id)"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeCateById(scope.row.cat_id)"></el-button>
            </template>
            </tree-table>
            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-row>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addCateDialogVisible" @close="addCateDialogClosed" width="50%" >
    <!--  -->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
        <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_pid">
            <!-- expandTrigger='hover'() v-model() :options()  :props() 
            clearable(X) change-on-select() -->
            <el-cascader expandTrigger='hover' v-model="selectedKeys" :options="parentCateList" :props="cascaderProps" @change="parentCateChange" clearable change-on-select></el-cascader>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
    </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editCateDialogVisible" @close="editCateDialogClosed" width="50%" >
    <!--  -->
    <el-form :model="editCateForm" :rules="editCateFormRules" ref="editCateFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
        <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
    </span>
    </el-dialog>
    </div>
</template>
           
<script>
export default{
    data(){
        return {
            cateList:[],
            queryInfo:{
                type:3,
                pagenum:1,
                pagesize:5
            },
            total:0,
            columns: [
                {label:'',prop:'cat_name'},
                //type:'template'()template:'isok'(isok)
                {label:'',prop:'',type:'template',template:'isok'},
                {label:'',prop:'',type:'template',template:'order'},
                {label:'',prop:'',type:'template',template:'opt'}
            ],
            addCateDialogVisible:false,
            addCateForm:{
                cat_name:'',
                cat_pid:0,
                cat_level:0
            },
            addCateFormRules:{
                cat_name:[
                    {required:true,message:'',trigger:'blur'}
                ],
                cat_pid:[
                    {required:true,message:'',trigger:'blur'}
                ]
            },
            parentCateList:[],
            cascaderProps:{
                value:'cat_id',
                label:'cat_name',
                children:'children',
                expandTrigger:'hover'
            },
            //
            selectedKeys:[],
            editCateDialogVisible:false,
            editCateForm:{
                cat_name:''
            },
            editCateFormRules:{
                cat_name:[
                    {required:true,message:'',trigger:'blur'}
                ]
            },
            
        }
    },
    created(){
        this.getCateList()
    },
    methods:{
        async getCateList(){
            const {data:res} = await this.$http.get('categories',{params:this.queryInfo})
            console.log(res)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.cateList = res.data.result
            this.total = res.data.total
        },
        handleSizeChange(newSize){
            this.queryInfo.pagesize = newSize
            this.getCateList()
        },
        handleCurrentChange(newPage){
            this.queryInfo.pagenum = newPage
            this.getCateList()
        },
        showCateDialog(){
            this.getParentCateList()
            this.addCateDialogVisible = true
        },
        async getParentCateList(){
            const {data:res}=await this.$http.get('categories',{params:{type:2}})
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.parentCateList = res.data
        },
        parentCateChange(){
             //
            console.log(this.selectedKeys)
            //
            if(this.selectedKeys.length > 0){
            //
            this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
            //level
            this.addCateForm.cat_level = this.selectedKeys.length
            }else{
            this.addCateForm.cat_pid = 0
            this.addCateForm.cat_level = 0
            }
        },
        addCateDialogClosed(){
            this.$refs.addCateFormRef.resetFields()
            this.selectedKeys = []
            this.addCateForm.cat_pid = 0
            this.addCateForm.cat_level = 0
        },
        addCate(){
            this.$refs.addCateFormRef.validate(async valid => {
                if(!valid) return
                const {data:res} = await this.$http.post('categories',this.addCateForm)
                if(res.meta.status !== 201){
                    return this.$message.error('')
                }
                this.$message.success('')
                this.getCateList()
                this.addCateDialogVisible = false
            })
        },
        async showCayeDialog(id){
            const {data:res} = await this.$http.get('categories/'+id)
            if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.editCateForm = res.data
            console.log(this.editForm)
            this.editCateDialogVisible = true
        },
        editCate(){
            this.$refs.editCateFormRef.validate(async valid => {
                if(!valid) return
                const {data:res} = await this.$http.put('categories/'+this.editCateForm.cat_id,this.editCateForm)
                if(res.meta.status !== 200){
                return this.$message.error('')
            }
            this.$message.success('')
                this.editCateDialogVisible = false
                this.getCateList()
            })
        },
        async removeCateById(id){
                const confirmResult = await this.$confirm('','',{
                    confirmButtonText:'',
                    cancelButtonText:'',
                    type:'warnimg'
                }).catch(err => err)
                if(confirmResult != "confirm"){
                    return this.$message.info('')
                }
                const {data: res } = await this.$http.delete('categories/'+id)
                if(res.meta.status !== 200) return this.$message.error('')
                this.$message.success('')
                this.getCateList()
            },
    }      
}
</script>
<style lang="less" scoped>
            
</style>